<template>
  <div class="wrapper" ref="wrapper">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  name: "scroll",
  props:{
    probeType:{
      type:Number,
      default:0
    },
    pullingup:{
      type:Boolean,
      default: false
    }
  },
  data(){
    return{
      scroll:null
    }
  },
  mounted() {
    this.scroll=new BScroll(this.$refs.wrapper,{
      click:true,
      probeType:this.probeType,
      pullUpLoad:this.pullingup
    })

      this.scroll.on(
        'scroll',(position)=>{
          this.$emit('scroll',position)
        }
      )

    if(this.pullingup){
      this.scroll.on(
        'pullingUp',()=>{
          this.$emit('pullingup')
        }
      )
    }
  },
  methods:{
    scrollTo(x,y,time=500){
     this.scroll && this.scroll.scrollTo(x,y,time)
    },
    finishpullup(){
      this.scroll.finishPullUp()
    },
    refresh(){
      this.scroll && this.scroll.refresh()
    }
  }
}
</script>

